import React, { Fragment } from 'react'
import { Link } from 'react-router-dom';
import { Zoom } from 'react-slideshow-image';

const images = [
    {
        url: 'https://retrina.com/oclms/oclms_one_instructor/image/catalog/demo/banners/hero-02.jpg',
        caption: 'The Great Fashion'
    },
    {
        url: 'https://themefie.com/html/edufie/assets/img/banner/0.jpg',
        caption: 'The Great Fashion '
    },
    {
        url: 'https://retrina.com/oclms/oclms_one_instructor/image/cache/catalog/demo/event/event-04-960x610.jpg',
        caption: 'The Great Fashion '
    },


];
const Slider = () => {
  return (
<Fragment>
  {/* <!-- Start of slider section
         ============================================= -->         */}

<section id="yl-slider" class="yl-slider-section">
                <div class="slider-main-item position-relative">
                    <div class="slider-main-img img-zooming">
                        <Zoom scale={0.4}>
                            {images.map((each, index) => (
                                <>
                                    <img key={index} style={{ width: "100%" }} src={each.url} />

                                </>
                            ))}
                        </Zoom>



                    </div>
                    <div class="slider-overlay"></div>

                    <div class="container" >

                        <div class="slider-main-text yl-headline pera-content">
                            <span>Our Progression</span>
                            <h1>Prosper in this volatile
                                market funding.
                            </h1>
                            <p>Collaborate Consulting exists to find the place where to being seemingly disparate interests meet.</p>
                            <div class="slider-main-btn">
                            <Link to="/aboutus"> <a href="#"><i class="fas fa-user"></i> About us</a></Link>

                            <Link to="/courses"> <a><i class="fas fa-cog"></i> Courses</a></Link>
                            </div>
                        </div>


                    </div>


                </div >
            </section >


            {/* <!-- End of Slider section
         ============================================= -->  */}



</Fragment>  )
}

export default Slider