import{combineReducers,applyMiddleware} from "redux";
import {legacy_createStore as createStore} from 'redux'

import thunk from "redux-thunk";
import {composeWithDevTools } from "redux-devtools-extension";
import {  allUsersReducer, loginReducer, userDetailsReducer } from "./Redux/Reducers/UserReducers";
import { CourseDetailsReducer, coursesReducer, myCourseReducer, newCourseReducer } from "./Redux/Reducers/CourseReducer";
import { cartReducer } from "./Redux/Reducers/CartReducer";
import { myOrdersReducer, newOrderReducer, orderDetailsReducer,allOrdersReducer } from "./Redux/Reducers/EnrollReducer";
const Reducer =combineReducers({
    user:loginReducer,
    mycourses:myCourseReducer,
    admincourse:coursesReducer,
    allcourses:coursesReducer,
    newcourse:newCourseReducer,
    allusers:allUsersReducer,
    coursedetails:CourseDetailsReducer,
    userdetail:userDetailsReducer,
    cart:cartReducer,
    newOrder: newOrderReducer,
    myOrders:myOrdersReducer,
    orderDetails:orderDetailsReducer,
    allOrders:allOrdersReducer 
});
const middleware =[thunk]
let initialState = {
  
};
const store = createStore(
    Reducer,
    initialState,
    composeWithDevTools(applyMiddleware(...middleware))
  );

export default store;
