import React, { Fragment } from 'react'
import av from '../images/av.jpg'
const Featureabout = () => {
  return (
<Fragment>
{/* <!-- Start of Feature section
         ============================================= --> */}
         <section id="yl-feature" class="yl-feature-section">
            <div class="container">
               <div class="yl-feature-content">
                  <div class="row justify-content-center wow fadeInUp" data-wow-delay="0ms" data-wow-duration="1500ms">
                     <div class="col-lg-4 col-md-6">
                        <div class="yl-feature-innerbox position-relative">
                           <div class="yl-feature-icon float-left">
                              <i class="flaticon-give"></i>
                           </div>
                           <div class="yl-feature-text yl-headline pera-content">
                              <h3><a href="#">Affordable Tuition</a></h3>
                              <p>Highly competitive rates yet, located in heart of the Nation's Capital.</p>
                           </div>
                        </div>
                     </div>
                     <div class="col-lg-4 col-md-6">
                        <div class="yl-feature-innerbox position-relative">
                           <div class="yl-feature-icon float-left">
                              <i class="flaticon-goal"></i>
                           </div>
                           <div class="yl-feature-text yl-headline pera-content">
                              <h3><a href="#">Success Driven</a></h3>
                              <p>The experiential learning  ensures  professional student success.</p>
                           </div>
                        </div>
                     </div>
                     <div class="col-lg-4 col-md-6">
                        <div class="yl-feature-innerbox position-relative">
                           <div class="yl-feature-icon float-left">
                              <i class="flaticon-presentation"></i>
                           </div>
                           <div class="yl-feature-text yl-headline pera-content">
                              <h3><a href="#">Business Partners</a></h3>
                              <p>Align with internationally-recognized businesses every step.</p>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      {/* <!-- End of Feature section
         ============================================= -->    */}
 {/* <!-- Start of about section
         ============================================= --> */}
         <section id="yl-about" class="yl-about-section">
            <div class="container">
               <div class="yl-about-content">
                  <div class="row">
                     <div class="col-lg-6">
                        <div class="yl-about-video-area position-relative wow fadeInLeft" data-wow-delay="0ms" data-wow-duration="1500ms">
                           <div class="yl-about-video-tag pera-content yl-headline">
                              <h4>21</h4>
                              <i class="fas fa-trophy"></i>
                              <span>Golden
                              years</span>
                           </div>
                           <div class="yl-video-img text-center">
                              <img src={'https://retrina.com/oclms/assets/img/1.jpg'} alt=""/>
                           </div>
                           <div class="yl-video-play-btn text-center">
                              <a class="video_box" href="https://www.youtube.com/watch?v=gUYjYEGbxEY">
                                 <i class="fas fa-play"></i>
                                 <span class="video_btn_border border_wrap-1"></span>
                                 <span class="video_btn_border border_wrap-2"></span>
                                 <span class="video_btn_border border_wrap-3"></span>
                              </a>
                           </div>
                        </div>
                     </div>
                     <div class="col-lg-6">
                        <div class="yl-about-text-area-content wow fadeInRight" data-wow-delay="0ms" data-wow-duration="1500ms">
                           <div class="yl-section-title yl-headline">
                              <span>Tour our institute</span>
                              <h2>We are innovative educational institution dedicated to the
                                 creation of the student.
                              </h2>
                           </div>
                           <div class="yl-about-text pera-content">
                              <p>We focus our efforts on shaping socially responsible and goal that oriented individuals into world-class professionals.</p>
                              <p>who have a strong entrepreneurial spirit, a global mindset and the ability to work both independently and in a team environment. We are committed to contributing to social development.</p>
                              <div class="yl-about-qoute">
                                 <span>"A University needs to be a centre of excellence
                                 for pursuit of knowledge."</span>
                                 <div class="yl-quote-author yl-headline">
                                    <h4><a href="#">Robert Yeast</a></h4>
                                    <span>(Vice-Chencellor)</span>
                                 </div>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      {/* <!-- End of About section
         ============================================= --> */}
</Fragment>
    )
}

export default Featureabout