import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { createOrder } from '../Redux/Actions/EnrollAction';
import { Typography } from '@mui/material';
import Header from '../Home/Header';
import Footer from '../Home/Footer';

const Enroll = ({ history }) => {
  const dispatch = useDispatch();
  const { cartItems, name, price, image, teacher, course, teacheremail,teacherdesignation } = useSelector((state) => state.cart);
  const order = {

    name: name,
    price: price,
    image: image,
    teacher: teacher,
    teacheremail: teacheremail,
    course: course,
    teacherdesignation:teacherdesignation
  };
  const submitHandler = (e) => {
    e.preventDefault();


    dispatch(createOrder(order));
    history.push("/login");

  };


  return (
    <>
    <Header/>
      <section id="course-page-course" class="course-page-course-section">
        <div class="container">
          <div class="course-page-course-content">
            
            <div class="course-page-courses-item">
              <div class="row">
               
                  <div class="col-lg-12 col-md-12">

                    <div class="yl-popular-course-img-text">
                    <form className="paymentForm" onSubmit={(e) => submitHandler(e)}>

                      <table class="table " >
                        <thead>
                          <tr>
                            <th>Course</th>
                            <th>Sttudent Name</th>
                            <th>Teacher</th>
                            <th>Teacher Designation</th>

                            <th>Teacher Email</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td> {name}</td>
                            <td>{price}</td>
                            <td>{teacher}</td>
                            <td>{teacherdesignation}</td>
                            <td>{teacheremail}</td>
                          </tr>

                        </tbody>

                      </table>

                      <button class="button-42" role="button" type="submit"  style={{float:'right'}}>Enroll Now</button>
<br></br>
</form>
                    </div>
                  </div>



                {/* ))} */}


              </div>
              
            </div>
          </div>
        </div>
      </section>
  <Footer/>

    </>

  )
}

export default Enroll