import {
    ADD_TO_CART,
    REMOVE_CART_ITEM,
    SAVE_SHIPPING_INFO,
  } from "../Constants/CartConstants";
  import axios from "axios";
  
  // Add to Cart
  export const addItemsToCart = (id) => async (dispatch, getState) => {
    const { data } = await axios.get(`/api/v1/course/${id}`);
  
    dispatch({
      type: ADD_TO_CART,
      payload: {
        course: data.course._id,
        name: data.course.name,
        price: data.course.price,
        image: data.course.images[0].url,
        stock: data.course.Stock,
        teacher: data.course.user.name,
        teacheremail: data.course.user.email,
        teacherdesignation: data.course.user.designation,
      },
    });
  
    localStorage.setItem("cartItems", JSON.stringify(getState().cart.cartItems));
  };
  
  // REMOVE FROM CART
  export const removeItemsFromCart = (id) => async (dispatch, getState) => {
    dispatch({
      type: REMOVE_CART_ITEM,
      payload: id,
    });
  
    localStorage.setItem("cartItems", JSON.stringify(getState().cart.cartItems));
  };
  
  // SAVE SHIPPING INFO
  export const saveShippingInfo = (data) => async (dispatch) => {
    dispatch({
      type: SAVE_SHIPPING_INFO,
      payload: data,
    });
  
    localStorage.setItem("shippingInfo", JSON.stringify(data));
  };
  