import React, { Fragment, useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { getUserDetails } from '../Redux/Actions/UserAction';
import Header from '../Home/Header';
import { getCourses } from '../Redux/Actions/CourseAction';
import Course from '../Courses/Course';
import { Link } from 'react-router-dom';
import { Typography } from '@mui/material';

const Instructordetail = ({ match }) => {
    const dispatch = useDispatch();
    const alert = useAlert()
    const { user, loading, error } = useSelector(
        (state) => state.userdetail
    );
    const {
        courses,

        coursesCount,
        resultPerPage,
        filteredcoursesCount,
    } = useSelector((state) => state.allcourses);
    useEffect(() => {
        dispatch(getCourses());

        dispatch(getUserDetails(match.params.id));
    }, [dispatch, match.params.id]);

    return (
        <Fragment>
            <Header />
            {/* <!-- Start of breadcrumb section
         ============================================= --> */}
            <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" >
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>Instructor Detail</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">Instructor Detail</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of breadcrumb section
         ============================================= --> */}{/* <!-- Start of instructor details section
         ============================================= --> */}
            <section id="instructor-details" class="instructor-details-section">
                <div class="container">
                    <div class="instructor-details-content position-relative">
                        <div class="row">
                            
                            <div class="col-lg-5">
                                <div class="instructor-details-img">
                                    <img src={user.avatar && user.avatar.url} alt="" style={{height:"662px"}} />
                                </div>
                            </div>
                            <div class="col-lg-7">
                                <div class="instructor-details-text">
                                    <div class="instructor-details-text-top clearfix">
                                        <div class="instructor-details-name-social  yl-headline float-left">
                                            <h3>{user.name}</h3>
                                            <span>Web Developer</span>
                                            <div class="instructor-details-social">
                                                <a href="#"><i class="fab fa-facebook-f"></i></a>
                                                <a href="#"><i class="fab fa-twitter"></i></a>
                                                <a href="#"><i class="fab fa-linkedin-in"></i></a>
                                                <a href="#"><i class="fab fa-instagram"></i></a>
                                            </div>
                                        </div>
                                        <div class="instructor-details-profile float-right">
                                            <span><i class="fas fa-list-ul"></i> 128 Course</span>
                                            <span><i class="fas fa-user"></i> 50000 Students</span>
                                            <span><i class="fas fa-star"></i><b>4.9</b> (235 review)</span>
                                        </div>
                                    </div>
                                    <div class="instructor-details-content-text pera-content">
                                        <p>Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est labo rum. Sed ut persp iciatis unde om nis iste natus erewror sit volup tatem accu santium doloremque laudantium</p>
                                        <p>Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, seytd quia co nsequuntur magni dolores eos qui ratione voluptatem sequi nesciunt. Neque poriero quisquam est, qui dolorem ipsum quia dolor sit amet, conse ctetur, adipisci velit, soreed quia non numquam eius modi tempora incidunt ubuit labore et dolore magnam aliquam quaerat voluptatem.</p>
                                        <div class="instructor-details-btn text-center">
                                            <a href="#">All Courses <i class="fas fa-chevron-right"></i></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of instructor details section
         ============================================= --> */}

            {/* <!-- Start of popular course by instructor section
         ============================================= -->           */}
            <section id="instructor-popular" class="instructor-popular-section">
                <div class="container">
                    <div class="instructor-popular-content yl-headline">
                        <h2>Popular Course by {user.name}</h2>
                        <div class="course-page-course-content">
                            <div class="course-page-courses-item">
                         

                                <div class="row">
                                    
                                    {courses && courses
                                        .filter(Course => {
                                            return (
                                                Course.user._id === user._id 
                                            );  
                                            
                                        })  
                                         .map((course, index) => {
                                            
                                            return (

                                                    <div class="col-lg-4 col-md-6">

                                                        <div class="yl-popular-course-img-text">
                                                            <div class="yl-popular-course-img text-center">
                                                                <img src={course.images[0] && course.images[0].url} alt="" />
                                                            </div>
                                                            <div class="yl-popular-course-text">
                                                                <div class="popular-course-fee clearfix">
                                                                    <span>Course Fee:  </span>
                                                                    <div class="course-fee-amount">
                                                                        <del>$59</del>
                                                                        <strong>$39</strong>
                                                                    </div>
                                                                </div>
                                                                <div class="popular-course-title yl-headline">
                                                                    <h3><Link to={`/coursedetail/${course._id}`}><a>{course.name}</a></Link>
                                                                    </h3>
                                                                    <div class="yl-course-meta">
                                                                        <a href="#"><i class="fas fa-file"></i>14 Lessons</a>
                                                                        <a href="#"><i class="fas fa-user"></i> 20 Students</a>
                                                                    </div>
                                                                </div>
                                                                <div class="popular-course-rate clearfix ul-li">
                                                                    <div class="p-rate-vote float-left">
                                                                        <ul>
                                                                            <li><i class="fas fa-star"></i></li>
                                                                            <li><i class="fas fa-star"></i></li>
                                                                            <li><i class="fas fa-star"></i></li>
                                                                            <li><i class="fas fa-star"></i></li>
                                                                            <li><i class="fas fa-star"></i></li>
                                                                        </ul>
                                                                        <span>(12 Votes)</span>
                                                                    </div>
                                                                    <div class="p-course-btn float-right">
                                                                        <a href="#"><i class="fas fa-chevron-right"></i></a>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        
                                                    </div>

                                            )
                                            
                                        })
                                
                                    
                                    }





                               
                                </div>


                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End popular course by instructor section
         ============================================= -->  */}

        </Fragment>)
}

export default Instructordetail