import React from 'react'
import Header from './Home/Header'
import Footer from './Home/Footer'
import { Link } from 'react-router-dom'
import notfound from "./images/er.png"
const My404Component = () => {
  return (
<>
<Header/>
{/* <!-- Start of breadcrumb section
         ============================================= --> */}
         <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" data-background="assets/img/banner/bn-bg1.jpg">
            <span class="breadcrumb-overlay position-absolute"></span>
            <div class="container">
               <div class="yl-breadcrumb-content text-center yl-headline"> 
                  <h2>404</h2>
                  <div class="yl-breadcrumb-item ul-li">
                     <ul class="breadcrumb">
                      <li class="breadcrumb-item"><a href="#">Home</a></li>
                      <li class="breadcrumb-item active" aria-current="page">Not Found</li>
                   </ul>
                </div>
             </div>
          </div>
       </section>
      {/* <!-- End of breadcrumb section
         ============================================= --> */}

{/* <!-- Start of not found content section
         ============================================= --> */}
         <section id="not-found" class="not-found-section">
            <div class="container">
               <div class="not-found-content text-center">
                  <span>Opps! things you are looking for doesn’t exists!</span>
                 <Link to="/"> <a>Return Home <i class="flaticon-arrow"></i></a></Link>
                  <div class="error-img">
                     <img src={notfound} alt=""/>
                  </div>
               </div>
            </div>
         </section>
      {/* <!-- End of not found content section
         ============================================= -->       */}

<Footer/>

</>  )
}

export default My404Component