import React, { Fragment } from 'react'
import Carousel from 'react-grid-carousel'

const images = [
    {
        url: 'https://html.themexriver.com/yle/assets/img/blg1.jpg',
        caption: 'Architecture ',
        subcaption: '4 Courses',
        detail: 'Up To 40% Off Final Sale Items.'
    },
    {
        url: 'https://html.themexriver.com/yle/assets/img/blg2.jpg',
        caption: 'Bussiness Studies ',
        subcaption: '2 Courses',
        detail: 'Up To 40% Off Final Sale Items.'
    },
    {
        url: 'https://html.themexriver.com/yle/assets/img/blg3.jpg',
        caption: 'Architecture ',
        subcaption: '4 Courses',
        detail: 'Up To 40% Off Final Sale Items.'
    },
    {
        url: 'https://html.themexriver.com/yle/assets/img/blg2.jpg',
        caption: 'Bussiness Studies ',
        subcaption: '2 Courses',
        detail: 'Up To 40% Off Final Sale Items.'
    },


];
const Blogs = () => {
    return (
        <Fragment>

            {/* <!-- Start of Blog section
         ============================================= -->  */}
            <section id="yl-blog" class="yl-blog-section">
                <div class="container">
                    <div class="yl-blog-content">
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="yl-section-title yl-headline">
                                    <span>Blog</span>
                                    <h2>You may learn more by our
                                        recent blog posting.
                                    </h2>
                                </div>
                            </div>
                        </div>
                        <div id="yl-blog-slider-id" class="yl-blog-slide-wrap">
                            <Carousel cols={3} rows={1} gap={20} loop>

                                {images.map(review => (

                                    <Carousel.Item key={review.id}>

                                        <div class="yl-blog-img-text">
                                            <div class="yl-blog-img text-center position-relative">
                                                <img class="product__items--img product__primary--img" src={review.url} alt="product-img" />
                                                <div class="yl-blog-date">
                                                    <i class="fas fa-calendar-alt"></i> 15 th Feb, 2021
                                                </div>
                                            </div>
                                            <div class="yl-blog-text yl-headline pera-content">
                                                <div class="yl-blog-meta text-uppercase">
                                                    <a href="#"><i class="far fa-user"></i> admin</a>
                                                    <a href="#"><i class="far fa-folder-open"></i> branding</a>
                                                </div>
                                                <div class="yl-blog-title">
                                                    <h3><a href="#">Five benefits of an IIE Higher
                                                        Certificate in Law.</a></h3>
                                                    <p>Lorem ipsum dolor sit amet, consectetur to
                                                        adipiscing elit, sed do eiusmod tempor tour
                                                        rincididunt ut labore.
                                                    </p>
                                                </div>
                                            </div>
                                        </div>


                                    </Carousel.Item>
                                ))}
                            </Carousel>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of Blog section
         ============================================= -->     */}

{/* <!-- Start of Newslatter section
         ============================================= -->      */}
         <section id="yl-newslatter" class="yl-newslatter-section">
            <div class="container">
               <div class="yl-newslatter-content">
                  <div class="row">
                     <div class="col-md-6">
                        <div class="yl-newslatter-text-icon">
                           <div class="yl-newslatter-icon float-left">
                              <i class="flaticon-email"></i>
                           </div>
                           <div class="yl-newslatter-text yl-headline">
                              <h3>Subscribe to a Newsletter!</h3>
                              <span>You will get our update instantly! <img src="assets/img/like.png" alt=""/></span>
                           </div>
                        </div>
                     </div>
                     <div class="col-md-6">
                        <div class="yl-newslatter-form position-relative">
                           <form action="#">
                              <input type="email" placeholder="Enter your mail address..."/>
                              <button>Submit <i class="fas fa-arrow-right"></i></button>
                           </form>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      {/* <!-- End of Newslatter section
         ============================================= --> */}

        </Fragment>)
}

export default Blogs