import React, { useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { getOrderDetails,clearErrors } from '../Redux/Actions/EnrollAction';
import { Link } from 'react-router-dom';

const MyEnrollDetails = ({match}) => {
    const { order, error, loading, orderorders,user } = useSelector((state) => state.orderDetails);
    const dispatch = useDispatch();
    const alert = useAlert();
    useEffect(() => {
      if (error) {
        alert.error(error);
        dispatch(clearErrors());
      }
  
      dispatch(getOrderDetails(match.params.id));
    }, [dispatch, alert, error, match.params.id]);
  return (
<>
Order #{order && order._id}
{/* <span>{order.user && order.user.name}</span> */}

{order&&order.name}
<img src={order&&order.image}/>

</>

    )
}

export default MyEnrollDetails