import React, { Fragment, useEffect, useState } from "react";
// import "./Header.css";
import { SpeedDial, SpeedDialAction } from "@material-ui/lab";
import Backdrop from "@material-ui/core/Backdrop";
import DashboardIcon from "@material-ui/icons/Dashboard";
import PersonIcon from "@material-ui/icons/Person";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import ListAltIcon from "@material-ui/icons/ListAlt";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import { Link, Redirect, useHistory } from "react-router-dom";

import { useAlert } from "react-alert";
import { logout } from "../Redux/Actions/UserAction";
import { useDispatch, useSelector } from "react-redux";
import Header from "../Home/Header";
import { getAllOrders } from "../Redux/Actions/EnrollAction";

const Profile = () => {

    const [open, setOpen] = useState(false);
    const history = useHistory();
    const alert = useAlert();
    const dispatch = useDispatch();
    const { user } = useSelector(
        (state) => state.user
    );
    const { orders } = useSelector(
        (state) => state.allOrders
    );
    
    function logoutUser() {
        dispatch(logout());
        alert.success("Logout Successfully");
        history.push("/")
    }
    useEffect(() => {
        dispatch(getAllOrders());
    }, [dispatch]);
    return (
        <Fragment>
            <Header />
            <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" >
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>My Profile</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">My Profile</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
                       <div class="container-fluid">
                <div class="page-header">
                    <div class="row">
                        <div class="col-lg-6 col-md-6 col-sm-6 col-12">
                            <h5 class="text-uppercase mb-0 mt-0 page-title">my Profile</h5>
                        </div>
                        
                    </div>
                </div>
                <div class="card-box m-b-0">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="profile-basic">
                                <div class="row">
                                    <div class="col-md-2">
                                        <div class="profile-info-left">
                                            <img src={user.avatar&&user.avatar.url} alt="" style={{borderRadius:"14px"}}/>
                                        </div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="profile-info-left">
                                            <h3 class="user-name m-t-0">{user&&user.name}</h3>
                                            <h5 class="company-role m-t-0 m-b-0">Smart School System</h5>
                                            <small class="text-muted">Student</small>
                                            {/* <div class="staff-id">Employee ID : HOD-0001</div> */}
                                        </div>
                                    </div>
                                    <div class="col-md-6">
                                        <ul class="personal-info">
                                            <li>
                                                <span class="title">Phone:</span>
                                                <span class="text"><a href="">{user&&user.phoneno}</a></span>
                                            </li>
                                            <li>
                                                <span class="title">Email:</span>
                                                <span class="text"><a href=""><span class="__cf_email__" data-cfemail="422827242427303b2f352d2c2502273a232f322e276c212d2f">{user&&user.email}</span></a></span>
                                            </li>
                                            <li>
                                                <span class="title">Birthday:</span>
                                                <span class="text">2nd August</span>
                                            </li>
                                            <li>
                                                <span class="title">Address:</span>
                                                <span class="text">5754 Airport Rd, Rahim YAr Khan</span>
                                            </li>
                                            <li>
                                                <span class="title">Gender:</span>
                                                <span class="text">Male</span>
                                            </li>
                                        </ul>
                                    </div>
                                    
                                </div>
                                <button class="button-42" role="button" onClick={logoutUser}>Logout</button>

                                
                            </div>
                        </div>
                    </div>
                </div>
                <div class="course-page-courses-item">
                <div class="row">
                            <h1>{user&&user.name} Enrollment</h1>
                    {orders && orders
                        .filter(order => {
                            return (
                                order.user._id === user._id
                            );
                        })
                        .map((course, index) => {
                            return (
                                <>




                                    <div class="col-lg-4 col-md-6">
                                        <div class="yl-popular-course-img-text">
                                            <div class="yl-popular-course-img text-center">
                                                <img src={course && course.image} alt="" />
                                            </div>
                                            <div class="yl-popular-course-text">
                                                <div class="popular-course-fee clearfix">
                                                    <span>Course Fee:  </span>
                                                    <div class="course-fee-amount">
                                                        <del>$59</del>
                                                        <strong>$39</strong>
                                                    </div>
                                                </div>
                                                <div class="popular-course-title yl-headline">
                                                    <h3><Link to={`/coursedetail/${course.course}`}><a>{course.name}</a></Link></h3>


                                                    <div class="yl-course-meta">
                                                        <a href="#"><i class="fas fa-file"></i>14 Lessons</a>
                                                        <a href="#"><i class="fas fa-user"></i> 20 Students</a>
                                                    </div>
                                                </div>
                                                <div class="popular-course-rate clearfix ul-li">
                                                    <div class="p-rate-vote float-left">
                                                        <ul>
                                                            <li><i class="fas fa-star"></i></li>
                                                            <li><i class="fas fa-star"></i></li>
                                                            <li><i class="fas fa-star"></i></li>
                                                            <li><i class="fas fa-star"></i></li>
                                                            <li><i class="fas fa-star"></i></li>
                                                        </ul>
                                                        <span>(12 Votes)</span>
                                                    </div>
                                                    <div class="p-course-btn float-right">
                                                        <a href="#"><i class="fas fa-chevron-right"></i></a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>



                                </>
                            );
                        })}


                </div>
                <div class="yl-course-pagination text-center ul-li">
                    <ul>
                        <li>
                            <a href="#">01</a>
                            <a href="#">02</a>
                            <a href="#">03</a>
                        </li>
                    </ul>
                </div>
            </div>
            </div>


        </Fragment>
    );
};


export default Profile