import React, { useState, useEffect, Fragment } from 'react'

import { useDispatch, useSelector } from 'react-redux'
import { Register, clearErrors, loginuser, logout } from './Redux/Actions/UserAction'
import { Link, useLocation } from 'react-router-dom'
import { useAlert } from 'react-alert'

import './login.css'
import Header from './Home/Header'
import Footer from './Home/Footer'
const Login = ({ location, history }) => {

    const dispatch = useDispatch();
    const alert = useAlert();

    const { error, loading, isAnthentication } = useSelector(
        (state) => state.user
    );

    const [loginEmail, setLoginEmail] = useState("");
    const [loginPassword, setLoginPassword] = useState("");


    const loginSubmit = (e) => {
        e.preventDefault();
        dispatch(loginuser(loginEmail, loginPassword));
    };

    location = useLocation();
    function logoutUser() {
        dispatch(logout());
        alert.success("Logout Successfully");
    }
    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        if (isAnthentication) {
            history.push("/profile");
        }
    }, [dispatch, error, alert, history, isAnthentication]);




    return (
        <Fragment>
            <Header />
            {/* <!-- Start of breadcrumb section
         ============================================= --> */}
            {/* <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" >
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>Sign in</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">Sign in</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section> */}
            {/* <!-- End of breadcrumb section
         ============================================= --> */}

            <div id="tabsContent" class="tab-content" style={{backgroundImage:"url(" + "https://academist.qodeinteractive.com/wp-content/plugins/academist-membership/assets/img/user-dashboard.jpg" + ")"}}>
            
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                
                
                <div id="login" class="tab-pane active show">
                    <div class="yl-login-content pera-content text-center">
                        <form onSubmit={loginSubmit} method="post">
                            <input type="text" placeholder="Username or email" value={loginEmail}
                                onChange={(e) => setLoginEmail(e.target.value)} />
                            <input type="text" placeholder="Password" required
                                value={loginPassword}
                                onChange={(e) => setLoginPassword(e.target.value)} />
                            <div class="yl-login-label clearfix">
                                <span><input type="checkbox" style={{color:"white"}}/>Remember me</span>
                                <a style={{color:"white"}}>Forget Password?</a>
                            </div>
                            <button type="submit" value="Login">Submit</button>
                        </form>
                        <p style={{color:"white"}}>Don’t have any account? <Link to="/registration"><a>Signup</a></Link></p>
                    </div>
                </div>
                {/* <div id="signUp" class="tab-pane fade">
                    <div class="yl-sign-up-content pera-content text-center">
                        <form action="#" method="post">
                            <input type="text" placeholder="First Name" />
                            <input type="text" placeholder="Last Name" />
                            <input type="email" placeholder="Email address" />
                            <input type="text" placeholder="Username" />
                            <input type="text" placeholder="Password" />
                            <input type="text" placeholder="Confirm password" />
                            <div class="yl-login-label clearfix">
                                <span><input type="checkbox" />By clicking signup you are agree to our terms of service</span>
                            </div>
                            <button type="submit">Submit</button>
                        </form>
                        <p>Already have an account? <a href="#">Log in</a></p>
                    </div>
                </div> */}
            </div>



<Footer/>







        </Fragment>
    )
}

export default Login