import React, { Fragment, useEffect, useState } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { clearErrors, getCourses } from '../Redux/Actions/CourseAction';
import { Link, useParams } from 'react-router-dom';
import Header from '../Home/Header';
import Footer from '../Home/Footer';



const categories = [
    "shoess",
    "Footwear",
    "Bottom",
    "Tops",
    "Attire",
    "Camera",
    "SmartPhones",
];
const Course = () => {

    const dispatch = useDispatch();

    const alert = useAlert();

    const [currentPage, setCurrentPage] = useState(1);
    const [price, setPrice] = useState([0, 25000]);

    const [ratings, setRatings] = useState(0);

    const [category, setCategory] = useState("");
    const {
        courses,
        loading,
        error,
        coursesCount,
        resultPerPage,
        filteredcoursesCount,
    } = useSelector((state) => state.allcourses);
    const { keyword } = useParams();

    const setCurrentPageNo = (e) => {
        setCurrentPage(e);
    };

    const priceHandler = (event, newPrice) => {
        setPrice(newPrice);
    };
    let count = filteredcoursesCount;
    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        dispatch(getCourses(keyword, currentPage, price, category, ratings));
    }, [dispatch, keyword, currentPage, price, category, ratings, alert, error]);


    return (
        <Fragment>
            <Header />
            
            {/* <!-- Start of breadcrumb section
         ============================================= --> */}
            <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" >
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>Courses</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">Courses</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of breadcrumb section
         ============================================= --> */}
            {/* !-- Start of course page section
         ============================================= --> */}
            <section id="course-page-course" class="course-page-course-section">
                <div class="container">
                    <div class="course-page-course-content">
                        <div class="course-page-course-content-top">
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="course-grid-top-bar">
                                        <div class="course-grid-showing-result">
                                            <div class="yl-course-grid-list float-left">
                                                <a href="#"><i class="fas fa-th"></i></a>
                                                <a href="#"><i class="fas fa-list"></i></a>
                                            </div>
                                            <span class="show-result-text">Showing 1-6 of 6 results</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="course-grid-top-bar">
                                        <div class="yl-course-search position-relative">
                                            <form action="#" method="post">
                                                <input type="text" placeholder="Enter your course"/>
                                                    <button>Search</button>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="course-page-courses-item">
                            <div class="row">
                            {courses &&
                                courses.map((course) => (
                                    <>




                                <div class="col-lg-4 col-md-6">
                                    <div class="yl-popular-course-img-text">
                                        <div class="yl-popular-course-img text-center">
                                        <img src={course.images[0] && course.images[0].url} alt="" />
                                        </div>
                                        <div class="yl-popular-course-text">
                                            <div class="popular-course-fee clearfix">
                                                <span>Course Fee:  </span>
                                                <div class="course-fee-amount">
                                                    <del>$59</del>
                                                    <strong>$39</strong>
                                                </div>
                                            </div>
                                            <div class="popular-course-title yl-headline">
                                            <h3><Link to={`/coursedetail/${course._id}`}><a>{course.name}</a></Link></h3>
                                                   
                                                
                                                <div class="yl-course-meta">
                                                    <a href="#"><i class="fas fa-file"></i>14 Lessons</a>
                                                    <a href="#"><i class="fas fa-user"></i> 20 Students</a>
                                                </div>
                                            </div>
                                            <div class="popular-course-rate clearfix ul-li">
                                                <div class="p-rate-vote float-left">
                                                    <ul>
                                                        <li><i class="fas fa-star"></i></li>
                                                        <li><i class="fas fa-star"></i></li>
                                                        <li><i class="fas fa-star"></i></li>
                                                        <li><i class="fas fa-star"></i></li>
                                                        <li><i class="fas fa-star"></i></li>
                                                    </ul>
                                                    <span>(12 Votes)</span>
                                                </div>
                                                <div class="p-course-btn float-right">
                                                    <a href="#"><i class="fas fa-chevron-right"></i></a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                


                                </>
                                ))}


                            </div>
                            <div class="yl-course-pagination text-center ul-li">
                                <ul>
                                    <li>
                                        <a href="#">01</a>
                                        <a href="#">02</a>
                                        <a href="#">03</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of course page section
         ============================================= -->         */}
<Footer/>

        </Fragment>


    )
}

export default Course