import React, { Fragment, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { myOrders } from '../Redux/Actions/CourseAction';
import { Link } from 'react-router-dom';

const TeacherDashboard = () => {
  const dispatch = useDispatch();

  const { loading, error, courses } = useSelector((state) => state.mycourses);

  useEffect(() => {
    if (error) {
        return alert.error(error)
    }
    dispatch(myOrders());
}, [dispatch, error]);
  return (
<Fragment>
<Link to="/addcourses"> Add Courses</Link><br></br>

{courses &&
                            courses.map((item) => (
                            <>
                            {item._id}<br></br>
                            {item.name}<br></br>
                            {item.description}<br></br>
                            {item.price}<br></br>
{item.user._id}                            
{item.user.name}                            
{item.user.email}                            
                            </>
                            
                            ))}
<h1>USMAN</h1>


</Fragment>
    )
}

export default TeacherDashboard