import React, { Fragment, useEffect } from 'react'
import Header from '../Home/Header'
import { getAllUsers } from '../Redux/Actions/UserAction';
import { useDispatch, useSelector } from 'react-redux';
import { useAlert } from 'react-alert';
import Footer from '../Home/Footer';
import { Link } from 'react-router-dom';

const Instructor = () => {
    const dispatch = useDispatch();
    const alert = useAlert();
const { users,error } = useSelector((state) => state.allusers);
useEffect(() => {


    dispatch(getAllUsers());
}, [dispatch, alert, error]);

    return (
        <Fragment>
            <Header />
            {/* <!-- Start of breadcrumb section
         ============================================= --> */}
            <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" >
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>Instructor</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">Instructor</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of breadcrumb section
         ============================================= --> */}
            {/* <!-- Start of instructors section
         ============================================= -->    */}
            <section id="yl-instructor" class="yl-instructor-section">
                <div class="container">
                    <div class="yl-instructor-content">
                        <div class="row">
                        {users
                .filter(user => {
                    return (
                        user.role === 'teacher' 
                    );
                })
                .map((user, index) => {
                    return (



                            <div class="col-lg-3 col-md-6">
                                <div class="yl-instructor-img-text text-center position-relative">
                                    <div class="yl-instructor-img position-relative">
                                    <img src={user.avatar && user.avatar.url} alt="" />
                                    </div>
                                    <div class="yl-instructor-text yl-headline position-relative">
                                        <h3><Link to={`/instructordetail/${user._id}`}><a>{user.name}</a></Link></h3>
                                        <span class="yl-ins-degi">Web Developer</span>
                                        <span class="yl-ins-course-no">60 Course</span>
                                    </div>
                                </div>
                            </div>

                    );
                })}

                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of instructors section
         ============================================= -->    */}


<Footer/>
        </Fragment>


    )
}

export default Instructor