import React, { useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { clearErrors, deleteOrder, getAllOrders, myOrders } from '../Redux/Actions/EnrollAction';
import { Link } from 'react-router-dom';
import { DELETE_ORDER_RESET } from '../Redux/Constants/Enroll';
import { getUserDetails } from '../Redux/Actions/UserAction';
const TeacherEnrolldetails = ({ history, match }) => {
    const dispatch = useDispatch();

    const alert = useAlert();
    const { isAnthentication, user } = useSelector((state) => state.user);

    const { error, orders } = useSelector((state) => state.allOrders);

    //   const { error: deleteError, isDeleted } = useSelector((state) => state.order);

    //   const deleteOrderHandler = (id) => {
    //     dispatch(deleteOrder(id));
    //   };

    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        // if (deleteError) {
        //   alert.error(deleteError);
        //   dispatch(clearErrors());
        // }

        // if (isDeleted) {
        //   alert.success("Order Deleted Successfully");
        //   history.push("/admin/orders");
        //   dispatch({ type: DELETE_ORDER_RESET });
        // }

        dispatch(getAllOrders());

    }, [dispatch, alert, error, match.params.id]);
    return (
        <>
            <h1>USMAN</h1>
            {orders && orders
                .filter(order => {
                    return (
                        order.teacher === user.name
                    );
                })
                .map((course, index) => {
                    return (
                        <>

                            {course&& course.name}<br></br>
                            <img src={course&& course.image}/><br></br>
                           Student Name: {course.user && course.user.name}<br></br>
                        </>



                    );
                })}

            {orders && orders.map((order) => (
                <>
                    {order && order.teacher}<br></br>
                    {order && order._id}
                </>
            ))}
        </>)
}

export default TeacherEnrolldetails