import React, { useState, useEffect, Fragment } from 'react'

import { useDispatch, useSelector } from 'react-redux'
import { Register, clearErrors, loginuser, logout } from './Redux/Actions/UserAction'
import { Link, useLocation } from 'react-router-dom'
import { useAlert } from 'react-alert'

import './login.css'
import Header from './Home/Header'
import Footer from './Home/Footer'
const Registration = ({ location, history }) => {
    const dispatch = useDispatch();
    const alert = useAlert();



    const [loginEmail, setLoginEmail] = useState("");
    const [loginPassword, setLoginPassword] = useState("");
    const [user, setUser] = useState({
        name: "",
        email: "",
        password: "",
        phoneno:"",
        designation:"",
        qualification:"",
    });
    const { name, email, password,phoneno,designation,qualification } = user;
    const [avatar, setAvatar] = useState("/Profile.png");
    const [avatarPreview, setAvatarPreview] = useState("/Profile.png");


    const loginSubmit = (e) => {
        e.preventDefault();
        dispatch(loginuser(loginEmail, loginPassword));
    };
    const registerSubmit = (e) => {
        e.preventDefault();

        const myForm = new FormData();

        myForm.set("name", name);
        myForm.set("email", email);
        myForm.set("phoneno", phoneno);
        myForm.set("designation", designation);
        myForm.set("qualification", qualification);
        myForm.set("password", password);
        myForm.set("avatar", avatar);
        dispatch(Register(myForm));
        history.push("/login");

    };

    const registerDataChange = (e) => {
        if (e.target.name === "avatar") {
            const reader = new FileReader();

            reader.onload = () => {
                if (reader.readyState === 2) {
                    setAvatarPreview(reader.result);
                    setAvatar(reader.result);
                }
            };

            reader.readAsDataURL(e.target.files[0]);
        } else {
            setUser({ ...user, [e.target.name]: e.target.value });
        }
    };
    // location = useLocation();
    // const redirect = location.search ? location.search.split("=")[1] : "/account";







    return (
        <Fragment>
            <Header />
            {/* <!-- Start of breadcrumb section
         ============================================= --> */}
            <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" >
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>Registration</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">Registration</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of breadcrumb section
         ============================================= --> */}
            <br></br>
            <br></br>
            <br></br>
            <div id="tabsContent" class="tab-content" >

                <div id="signUp">
                    <div class="yl-sign-up-content pera-content text-center">
                        <form encType="multipart/form-data"
                            onSubmit={registerSubmit} method="post">
                            <input type="text" placeholder="First Name"
                                required
                                name="name"
                                value={name}
                                onChange={registerDataChange} />


                            <input type="number" placeholder="Phone Number"
                                required
                                name="phoneno"
                                value={phoneno}
                                onChange={registerDataChange} />
                            <input type="text" placeholder="Designation"
                                required
                                name="designation"
                                value={designation}
                                onChange={registerDataChange} />
                            <input type="text" placeholder="Qualification"
                                required
                                name="qualification"
                                value={qualification}
                                onChange={registerDataChange} />
                            <input type="email" placeholder="Email address"
                                required
                                name="email"
                                value={email}
                                onChange={registerDataChange} />
                            <input type="text" placeholder="Password" required
                                name="password"
                                value={password}
                                onChange={registerDataChange} />
                            <div id="registerImage">
                                <img src={avatarPreview} alt="Avatar Preview" style={{height:"57px",float:"left"}} />
                                <input
                                    type="file"
                                    name="avatar"
                                    accept="image/*"
                                    required
                                    onChange={registerDataChange}
                                />
                            </div>



                            <div class="yl-login-label clearfix">
                                <span><input type="checkbox" />By clicking signup you are agree to our terms of service</span>
                            </div>
                            <button type="submit" value="Register">Submit</button>
                        </form>
                        <p>Already have an account? <a href="#">Log in</a></p>
                    </div>
                </div>
            </div>
<br></br>
<br></br>
<br></br>
<br></br>
<br></br>
<br></br>


            <Footer />







        </Fragment>
    )
}

export default Registration