import React, { Fragment, useEffect, useState } from 'react'
import CountUp from 'react-countup';
import ScrollTrigger from 'react-scroll-trigger';

const Registration = ({ delayResend = "180" }) => {
    const [delay, setDelay] = useState(+delayResend);
    const houre = Math.floor(delay / 120);
    const minutes = Math.floor(delay / 60);
  
    const seconds = Math.floor(delay % 60);
    useEffect(() => {
      const timer = setInterval(() => {
        setDelay(delay - 1);
      }, 1000);
  
      if (delay === 0) {
        clearInterval(timer);
      }
  
      return () => {
        clearInterval(timer);
      };
    });
  
  return (
<Fragment>

{/* <!-- Start of Registration section
         ============================================= -->         */}
         <section id="yl-registration" class="yl-registration-section position-relative">
            <div class="container">
               <div class="yl-registration-content">
                  <div class="row">
                     <div class="col-lg-6">
                        <div class="yl-registration-countdown">
                           <div class="yl-section-title yl-headline">
                              <span>Registration</span>
                              <h2>Welcome to the Varsity
                                 Online Registration Portal.
                              </h2>
                           </div>
                           <div class="yl-registration-countdown-wrap">
                              <div class="coming-countdown ul-li">
                                 <ul>
                                    <li class="days">
                                       <span class="arch-count-down-number">6</span>
                                       <span class="count-unit">Days</span>
                                    </li>
                                    <li class="hours">
                                       <span class="arch-count-down-number">{houre}</span>
                                       <span class="count-unit">Hours</span>
                                    </li>
                                    <li class="minutes">
                                       <span class="arch-count-down-number">{minutes}</span>
                                       <span class="count-unit">Minutes</span>
                                    </li>
                                    <li class="seconds">
                                       <span class="arch-count-down-number">{seconds}</span>
                                       <span class="count-unit">Seconds</span>
                                    </li>
                                 </ul>
                              </div>
                           </div>
                        </div>
                     </div>
                     <div class="col-lg-6">
                        <div class="yl-registration-text-wrap yl-headline pera-content">
                           <h3>Get Today 25% Discount for
                              Corona Situation!
                           </h3>
                           <p>who have a strong entrepreneurial spirit, a global mindset and the ability to work both independently and in a team 
                              environment. We are committed to contributing to social 
                              development community colleges offer.
                           </p>
                           <a href="#">Register Now</a>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      {/* <!-- End of Registration section
         ============================================= -->  */}

</Fragment>

    )
}

export default Registration