import './App.css';
import { Fragment, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import ScreenA from './ScreenA';
import ScreenB from './ScreenB';
import Login from './login';
import { useSelector } from 'react-redux';
import PrivateRoute from './PrivateRoute';
import { loadUser } from './Redux/Actions/UserAction';
import store from "./Store";
import Profile from './User/Profile';
import Main from './Home/Main';
import About from './About/About';
import TeacherDashboard from './Teacher/TeacherDashboard';
import Admin from './Admin/Admin';
import AddCourses from './Admin/AddCourses';
import Course from './Courses/Course';
import CourseDetails from './Courses/CourseDetails';
import Instructor from './Instructor/Instructor';
import Instructordetail from './Instructor/Instructordetail';
import Registration from './Registration';
import Enroll from './Enroll/Enroll';
import Success from './Enroll/Success';
import MyEnroll from './Enroll/MyEnroll';
import MyEnrollDetails from './Enroll/MyEnrollDetails';
import TeacherEnrolldetails from './Teacher/TeacherEnrolldetails';
import Contact from './Contact/Contact';
import My404Component from './My404Component';
import ScrollToTop from './Scrollup';
import Event from './Event/Event';
import Zoom from './Event/Zoom';
function App() {
  const { isAnthentication , user} = useSelector((state) => state.user);
  useEffect(() => {


    store.dispatch(loadUser());
  }, []);
  return (
 


  
<Router>
<ScrollToTop/>
<Switch>

        <PrivateRoute
          isAdmin={true}
          exact
          path="/admin/dashboard"
          component={Admin}
        />
        <PrivateRoute
         
          exact
          path="/addcourses"
          component={AddCourses}
        />
<PrivateRoute path="/profile" component={Profile} />



        <Route exact path="/" component={Main} />
        <Route exact path="/enroll" component={Enroll} />
        <Route exact path="/success" component={Success} />
        <Route exact path="/orders" component={MyEnroll} />
        <Route exact path="/order/:id" component={MyEnrollDetails} />
        <Route exact path="/aboutus" component={About} />
        <Route exact path="/login" component={Login} />
        <Route exact path="/event" component={Event} />
        <Route exact path="/zoomwithus" component={Zoom} />
        <Route exact path="/registration" component={Registration} />
        <Route exact path="/courses" component={Course} />
        <Route exact path="/instructor" component={Instructor} />
        <Route exact path="/contactus" component={Contact} />
        <Route exact path="/teacher/enroll" component={TeacherEnrolldetails} />
        <Route exact path="/coursedetail/:id" component={CourseDetails} />
        <Route exact path="/instructordetail/:id" component={Instructordetail} />
        <PrivateRoute isTeacher={true} exact path="/teacher/dashboard" component={TeacherDashboard} />
        <Route path='*' exact={true} component={My404Component} />

     

        </Switch>
</Router>

);
}

export default App;
