import Footer from '../Home/Footer'
import { Link } from 'react-router-dom'
import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import { useAlert } from 'react-alert';
import Header from '../Home/Header';
const Contact = () => {
    const form = useRef();
    const alert =useAlert();

    const sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_7z09dcg', 'template_2vmxj7d', form.current, '6gMyESo8J8U6AtKI7')
            .then((result) => {
                console.log(result.text);
                alert.success("Message Sent")
            }, (error) => {
                console.log(error.text);
            });
    };
    return (
        <>
        <Header/>
            {/* <!-- Start of breadcrumb section
         ============================================= --> */}
            <section id="yl-breadcrumb" class="yl-breadcrumb-section position-relative" data-background="assets/img/banner/bn-bg1.jpg">
                <span class="breadcrumb-overlay position-absolute"></span>
                <div class="container">
                    <div class="yl-breadcrumb-content text-center yl-headline">
                        <h2>Contact Us</h2>
                        <div class="yl-breadcrumb-item ul-li">
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item"><a href="#">Home</a></li>
                                <li class="breadcrumb-item active" aria-current="page">Contact Us</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- End of breadcrumb section
         ============================================= --> */}
            {/* <!-- Start of contact map section */}
            {/* ============================================= -->    */}
            <div class="yl-googlemap">
                <div class="container">

                    <div data-lat="43.653225" data-lon="-79.383186" data-zoom="12" data-info="Trinity Bellwoods Park" data-mlat="43.653225" data-mlon="-79.383186" data-marker="assets/img/map-m.png">
                    <iframe id="yl_map"  class="contact__map--iframe" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d56146.70531960679!2d70.26898303875154!3d28.414154513318934!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39375c713b45db39%3A0x28af23c1672a0170!2sRahim%20Yar%20Khan%2C%20Punjab%2C%20Pakistan!5e0!3m2!1sen!2s!4v1681274040732!5m2!1sen!2s" style={{ width: "600", height: "450", border: "0", }} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>

                    </div>
                </div>
            </div>
            {/* <!-- End of contact map section */}
            {/* <!-- Start of contact content section */}
            {/* ============================================= -->        */}
            <section id="contact-content" class="contact-content-section">
                <div class="container-fluid">
                    <div class="yl-section-title text-center yl-headline yl-title-style-two position-relative">
                        <p class="title-watermark">Categories</p>
                        <span>Top Categories </span>
                        <h2>We have many courses in our
                            top Categories
                        </h2>
                    </div>
                    <div class="yl-contact-content-wrap">
                        <div class="row justify-content-center">
                            <div class="col-lg-4 col-md-6">
                                <div class="yl-contact-content-inner text-center">
                                    <div class="yl-contact-content-icon">
                                        <img src="assets/img/cct-icon1.png" alt="" />
                                    </div>
                                    <div class="yl-contact-content-text yl-headline">
                                        <h3>Address</h3>
                                        <span>
                                            212, Rahim Yar Khan, Pakistan
                                        </span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-lg-4 col-md-6">
                                <div class="yl-contact-content-inner text-center">
                                    <div class="yl-contact-content-icon">
                                        <img src="assets/img/cct-icon2.png" alt="" />
                                    </div>
                                    <div class="yl-contact-content-text yl-headline">
                                        <h3>Email Us</h3>
                                        <span>usmanbaber@smartSchool.com</span>
                                        <span>sales@smartSchool.com</span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-lg-4 col-md-6">
                                <div class="yl-contact-content-inner text-center">
                                    <div class="yl-contact-content-icon">
                                        <img src="assets/img/cct-icon3.png" alt="" />
                                    </div>
                                    <div class="yl-contact-content-text yl-headline">
                                        <h3>Phone No</h3>
                                        <span>+92 3043290239</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="yl-contact-form-wrap yl-headline">
                        <h3>Write us a message</h3>
                        <form class="yl-contact-form-area" ref={form} onSubmit={sendEmail} method="post" >
                        <div class="yl-contact-form-input d-flex">
                            <input type="text" placeholder="Your Name*" name="user_name" />
                            <input type="email" placeholder="Your email*"name="user_email" />
                            <input type="text" placeholder="Phone" />
                        </div>
                        <textarea  placeholder="Write your message here*"  name="message"></textarea>
                        <button type="submit"  value="Send">Submit Now <i class="fas fa-arrow-right"></i></button>
                    </form>
                </div>
            </div>
        </section >
        {/* <!-- End of contact content section
         ============================================= -->     */}
<Footer/>

</>

    )
}

export default Contact